export const query = graphql`
  query {
    allStrapiCategories(filter: { level: { eq: 1 } }) {
      nodes {
        id
        level
        labelTa
        label
        slug: name
        childs {
          id
          labelTa
          label
          slug: name
        }
      }
    }
    allCategoryImages: allFile(
      filter: { relativeDirectory: { eq: "categories" } }
    ) {
      edges {
        node {
          id
          name
          childImageSharp {
            fixed(height: 200, width: 250, quality: 100) {
              ...GatsbyImageSharpFixed_withWebp
            }
          }
        }
      }
    }
    banner: file(relativePath: { eq: "header/header.jpg" }) {
      childImageSharp {
        # Specify the image processing specifications right in the query.
        # Makes it trivial to update as your page's design changes.
        fluid(maxWidth: 2000, quality: 100) {
          ...GatsbyImageSharpFluid
          srcSetWebp
          srcWebp
        }
      }
    }
  }
`;

export default IndexPage;
